import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { Form, Input, DatePicker, Button, Image, Upload } from 'antd';
import PropTypes from 'prop-types';
import moment from 'moment';
import './form.css';

const FormReadLog = (props) => {
    const { initialValues, onFinish, onFinishFailed, onUpload, mode } = props;
    const [form] = Form.useForm();
    const [bookName, setBookName] = useState(null);
    const [author, setAuthor] = useState(null);
    const [canSubmit, setCanSubmit] = useState(false);

    useEffect(() => {
        form.setFieldsValue({
            ...initialValues,
            start_date: moment(initialValues?.start_date),
            end_date: initialValues?.end_date ? moment(initialValues?.end_date) : null
        });
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [initialValues]);

    const validateBookName = (e) => {
        if (mode !== "create") return false;
        let value = e.target.value;
        setBookName(e.target.value);
        if (
            value !== null && value !== "" &&
            author !== null && author !== ""
        ) {
            setCanSubmit(true);
        } else {
            setCanSubmit(false);
        }
    };

    const validateAuthor = (e) => {
        if (mode !== "create") return false;
        let value = e.target.value;
        setAuthor(e.target.value);
        if (
            value !== null && value !== "" &&
            bookName !== null && bookName !== ""
        ) {
            setCanSubmit(true);
        } else {
            setCanSubmit(false);
        }
    };

    return (
        <>
            <div className="section-img">
                {
                    initialValues?.cover ? (
                        <div style={{ marginBottom: "30px" }}>
                            <Image alt='' height={400} src={initialValues?.cover?.base64 ? initialValues?.cover?.base64 : `data:${initialValues?.cover?.mimetype};base64,${initialValues?.cover?.img}`} />
                        </div>
                    ) : (
                        <div style={{ marginBottom: "30px" }}>
                            <Image
                                alt=''
                                src="error"
                                fallback="data:image/png;base64,"
                            />
                        </div>
                    )

                }
                <Upload
                    name="cover"
                    onChange={onUpload}
                    beforeUpload={() => false}
                    showUploadList={false}
                    multiple={false}
                    maxCount={1}
                >
                    <Button type="primary"></Button>
                </Upload>
            </div>
            <Form
                labelCol={{ span: 8 }}
                wrapperCol={{ span: 10 }}
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
                form={form}
            >
                <Form.Item label="" name="book_name" required>
                    <Input type="text" onChange={validateBookName} />
                </Form.Item>
                <Form.Item label="" name="author" required>
                    <Input type="text" onChange={validateAuthor} />
                </Form.Item>
                <Form.Item label="" name="start_date" required>
                    <DatePicker allowClear={false} />
                </Form.Item>
                <Form.Item label="" name="end_date">
                    <DatePicker />
                </Form.Item>
                <Form.Item wrapperCol={{ span: 100 }} style={{ textAlign: "center" }}>
                    <Button type="" style={{ marginRight: "10px" }}><Link to="/"></Link></Button>
                    <Button type="primary" htmlType="submit" disabled={!canSubmit && mode === "create"}></Button>
                </Form.Item>
            </Form>

        </>
    )
};

FormReadLog.propTypes = {
    onFinish: PropTypes.func,
    onFinishFailed: PropTypes.func,
    onUpload: PropTypes.func,
    initialValues: PropTypes.object,
    mode: PropTypes.string
};

FormReadLog.defaultProps = {
    onFinish: null,
    onFinishFailed: null,
    onUpload: null,
    initialValues: null,
    mode: "create"
};

export default FormReadLog;